import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AddNotificationComponent } from './add-notification.component';
import { GeneralNotificationComponent } from './general-notification/general-notification.component';
import { SelectProgramNotificationComponent } from './select-program-notification/select-program-notification.component';

const routes: Routes = [
  { path: '', component: AddNotificationComponent, children: [
    { path: '', redirectTo: 'general', pathMatch: 'full' },
    { path: 'general', component: GeneralNotificationComponent },
    { path: 'select-program', component: SelectProgramNotificationComponent }
  ]
}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AddNotificationRoutingModule { }
